body {
  background-color: #3A87B2;
  overflow: auto;
  padding: 5px;
  font: 14px/18px "Helvetica Neue",Arial,Helvetica,sans-serif;
  color: #393939;
}

.flexbox {
  display: flex;
  justify-content: space-between;
  background-color: #EAEEF3;
}
.list {
  align-self: flex-start;
  background-color: #E3E3E3;
  border-radius: 3px;
  margin: 5px 5px;
  padding: 10px;
  width: 260px;
  &__cards {
    overflow-x: hidden;
    overflow-y: auto;
  }
  &__title {
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    cursor: grab;
  }
  &__add {
    border-radius: 0 0 3px 3px;
    color: #838c91;
    display: block;
    flex: 0 0 auto;
    padding: 8px 10px;
    position: relative;
    text-decoration: none;
    user-select: none;
    &:hover {
      background-color: #cdd2d4;
      color: #4d4d4d;
      text-decoration: underline;
    }
  }
}
.card {
  border-radius: 3px;
  margin-top: 5px;
  background-color: #FFF;
  border-bottom: 1px solid #CCC;
  padding: 6px 8px;
  cursor: pointer;
  &:hover {
    background-color: #F0F0F0;
  }
  &__detail {
    font-size: 12px;
    color: #939393;
  }
}
.hidden {
  display: none;
}